<template>
    <div>
        <h1>Empréstimos</h1>
        <table>
            <thead>
                <tr>
                    <th>Livro</th>
                    <th>Usuário</th>
                    <th>Data de Empréstimo</th>
                    <th>Ações</th>
                </tr>
            </thead>
            <tbody>
                <tr v-for="emprestimo in emprestimosFiltrados" :key="emprestimo.id">
                    <td>{{ emprestimo.livro }}</td>
                    <td>{{ emprestimo.usuario }}</td>
                    <td>{{ emprestimo.dataEmprestimo }}</td>
                    <td>
                        <button @click="devolverEmprestimo(emprestimo.id)">Devolver</button>
                    </td>
                </tr>
            </tbody>
        </table>

        <button class="botao-adicionar" @click="abrirModal">+</button>

        <!-- Modal para Cadastrar Empréstimo -->
        <div v-if="modalAberta" class="modal">
            <div class="conteudo-modal">
                <span class="close" @click="fecharModal">&times;</span>
                <h2>Cadastrar Empréstimo</h2>
                <form @submit.prevent="cadastrarEmprestimo">
                    <div class="campo-cadastro">
                        <label for="livro">Livro:</label>
                        <input type="text" v-model="novoEmprestimo.livro" required />
                    </div>
                    <div class="campo-cadastro">
                        <label for="usuario">Usuário:</label>
                        <input type="text" v-model="novoEmprestimo.usuario" required />
                    </div>
                    <div class="campo-cadastro">
                        <label for="dataEmprestimo">Data de Empréstimo:</label>
                        <input type="date" v-model="novoEmprestimo.dataEmprestimo" required />
                    </div>
                    <button class="botao-padrao" type="submit">Cadastrar</button>
                </form>
            </div>
        </div>
    </div>
</template>

<script>
    import emprestimosService from '../services/emprestimosService';

    export default {
        data() {
            return {
                emprestimos: [],
                modalAberta: false,
                novoEmprestimo: {
                    livro: '',
                    usuario: '',
                    dataEmprestimo: ''
                }
            };
        },
        async mounted() {
            await this.carregarEmprestimos();
        },
        methods: {
            async carregarEmprestimos() {
                try {
                    const response = await emprestimosService.obterTodos();
                    this.emprestimos = response.data;
                } catch (error) {
                    console.error('Erro ao carregar empréstimos:', error);
                }
            },
            async cadastrarEmprestimo() {
                try {
                    await emprestimosService.cadastrar(this.novoEmprestimo);
                    this.fecharModal();
                    this.novoEmprestimo = {
                        livro: '',
                        usuario: '',
                        dataEmprestimo: ''
                    };
                    await this.carregarEmprestimos();
                } 
                catch (error) {
                    console.error('Erro ao cadastrar empréstimo:', error);
                }
            },
            abrirModal() {
                this.modalAberta = true;
            },
            fecharModal() {
                this.modalAberta = false;
            },
            async devolverEmprestimo(id) {
                try {
                    await emprestimosService.devolver(id);
                    await this.carregarEmprestimos();
                } 
                catch (error) {
                    console.error('Erro ao devolver empréstimo:', error);
                }
            }
        }
    };
</script>

<style>
    form {
        max-width: 600px;
        margin: auto;
        padding: 1em;
        background: #f9f9f9;
        border-radius: 5px;
    }

    input {
        border: 1px solid #CCCCCC;
        padding: .5em;
        font-size: 1em;
        width: 100%;
        box-sizing: border-box;
    }

    .close {
        position: absolute;
        top: 10px;
        right: 10px;
        cursor: pointer;
    }
</style>
